<template>
  <div id="app">
    <headPage></headPage>
    <router-view/>
  </div>
</template>

<script>
//App.vue是我们的主组件，所有页面都是在App.vue下进行切换的。
//引用组件
import headPage from "./components/headPage.vue";

export default {
  name: "App",

  //把引用的组件给申明到 components 里面去。这样，我们就可以在 template 里面使用了
  components: {
    headPage
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



