import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/main'
import showNewsByNewsType from '@/components/news/showNewsByNewsType'
import login from '@/components/user/free/login'
import manageMain from '@/components/user/manageUIMain/manageMain'
import showUserInformation from '@/components/user/manage/showUserInformation'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: main
    },
    {
      path: '/showNewsByNewsType',
      name: 'showNewsByNewsType',
      component: showNewsByNewsType     
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/manageMain',
      name: 'manageMain',
      component: manageMain,
        //嵌套路由，children中的组件会显示在manageMain组件中的<router-view></router-view>中，而不是App组件的<router-view></router-view>中
        children: [
          {
            path: 'showUserInformation',
            name: 'showUserInformation',
            component: showUserInformation
          }          
        ]      
    }
  ]
})



