<template>
  <div id="headPage">
    <div class="div-out">
      <div class="logRight">
        <div class="logRightInner">
          <template v-if="user">
            <!-- router-link类似a标签，点击可以跳转路由， /manageMain是router/index.js中定义的路由路径  -->
            <router-link to="/manageMain">管理</router-link>
          </template>

          <a href="/NewsSystem/index.jsp">首页</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <template v-if="!user">
            <router-link to="/login">登录</router-link>&nbsp;&nbsp;&nbsp;
            &nbsp;
            <a href="/NewsSystem/user/free/register.jsp">注册</a>
          </template>
          <template v-else>
            {{user.name}}&nbsp;&nbsp;
            <a href="/NewsSystem/servlet/UserServlet?condition=exit">注销</a>
          </template>
        </div>
      </div>
    </div>
    <div class="clear"></div>
  </div>
</template>

<script>
//用于vuex的状态管理
import { mapState, mapMutations, mapGetters } from "vuex";

export default {
  name: "headPage",

  data() {
    return {
      user: {}
    };
  },

  created: function() {
    this.getUser();
  },

  methods: {
    getUser: function() {
      var url = "/servlet/UserServlet?condition=getUser";
      //利用axios发出ajax的get请求
      this.$http
        .get(url)
        .then(response => {
          this.user = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style scoped>
.cursor:pointer {
  cursor: pointer;
  width: 20px;
  height: 20px;
}
</style>


