// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

//项目文件夹说明：https://blog.csdn.net/Forever_SJ521/article/details/78932106
//main.js 这是项目的核心文件。全局的配置都在这个文件里面配置。
import Vue from "vue";
import App from "./App";

//官方的路由管理器，安装方式：npm install vue-router
//官网：https://router.vuejs.org/zh/installation.html
import router from "./router";

//axios进行http请求的工具，安装命令：npm install axios      相关网址如下：
//https://www.jianshu.com/p/7a9fbcbb1114
//https://github.com/axios/axios
//https://cn.vuejs.org/v2/cookbook/using-axios-to-consume-apis.html
import axios from "axios"; //引入axios  异步请求
import "./css/1.css"; //引入css

// 设置全局变量，全局变量定义在base.vue中，其他组件可以访问
import global_ from './components/base.vue'
Vue.prototype.GLOBAL = global_;
//设置服务器端地址为axios的访问体质
axios.defaults.baseURL=global_.BASE_URL;
//保持客户端访问服务器端时，保持session有效
axios.defaults.withCredentials=true;

Vue.config.productionTip = false;
//全局绑定axios到vue，以后使用this.$http来表示axios
Vue.prototype.$http = axios;
Vue.config.devtools = true

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router, //使用路由
  components: { App },
  template: "<App/>"
});



