<template>
  <div id="login">
    <form id="form">
      <div class="center loginDiv" style="width:550px;margin-top:40px">
        <table border="0" align="center" cellpadding="5" cellspacing="0">
          <tr>
            <td colspan="2" align="center">登录</td>
          </tr>
          <tr>
            <td align="right">用户名：</td>
            <td align="left">
              <!-- v-model表示绑定表单元素和本组件的数据login.name，   -->
              <input type="text" v-model="login.name">
            </td>
          </tr>
          <tr>
            <td align="right">密码：</td>
            <td align="left">
              <!-- v-model表示绑定表单元素和本组件的数据login.password，   -->
              <input type="password" v-model="login.password">
            </td>
          </tr>
          <tr>
            <td align="right">图形验证码：</td>
            <td valign="middle">
              <!-- v-model表示绑定表单元素和本组件的数据login.checkCode   -->
              <input type="text" v-model="login.checkCode" required="required">
              <!-- this.GLOBAL.BASE_URL 表示base.vue中定义的全局变量GLOBAL.BASE_URL（服务器端部分网址）   -->
              <img
                id="checkImg"
                :src="this.GLOBAL.BASE_URL+'/servlet/ImageCheckCodeServlet?rand=-1'"
                class="hand"
              >
            </td>
          </tr>
          <tr>
            <td colspan="2" align="center">
              <!-- v-on:click绑定鼠标点击事件   -->
              <input v-on:click="submitForm" type="button" value=" 登 录 ">
            </td>
          </tr>
        </table>
      </div>
    </form>
  </div>
</template>

<script>
//qs是一个npm仓库所管理的包，安装方式：npm install qs命令进行安装，
//引入qs工具，进行提交数据的格式转换
import Qs from "qs";

export default {
  name: "login",

  data() {
    return {
      login: {
        //表单数据整合成一个对象login
        name: "",
        password: "",
        checkCode: ""
      }
    };
  },

  methods: {
    submitForm: function() {
      console.log(this.login);
      //qs.stringify()将对象 序列化成查询字符串，如：'name=hehe&age=10'
      var data = Qs.stringify(this.login);
      console.log(data);

      var url = "/servlet/UserServlet?condition=login";
      //利用axios发出ajax的post请求
      this.$http
        .post(url, data, {
          headers: { "Content-Type": "application/x-www-form-urlencoded" }
        }) //设置提交Content-Type类型，无文件域的一般表单，可以设置为'application/x-www-form-urlencoded'
        .then(response => {
          // 响应成功回调
          if (response.data.result == 1) {
            //成功登陆后，1.更新headPage.vue组件中关于用户的信息；2、显示home.vue组件
            console.log(response.data);
            
            //使得App.vue中的<router-view/>显示home.vue组件
            this.$router.push({ path: "/" });
          } //登录失败
          else alert(data.message);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>



