<template>
  <div class="center" style="width:810px">
    <div class="newsShowByType_frame center" id="frameDiv">
      <div class="newsShowByType_left" id="leftDiv">
        <ul style="list-style-type: none;">
          <li @click="newsTypeClick('all')">全部</li>
          <li @click="newsTypeClick('国际')">国际</li>
          <li @click="newsTypeClick('社会')">社会</li>
          <li @click="newsTypeClick('体育')">体育</li>
          <li @click="newsTypeClick('汽车')">汽车</li>
        </ul>
      </div>
      <div class="newsShowByType_rightTop" id="newsShowByType">
        <div>
          <ul id="newsShowByTypeUL">
            <template v-for="news in newses">
              <li>
                <a
                  :href="'/NewsSystem/servlet/NewsServlet?condition=showANews&newsId='+news.newsId+'&page=1&pageSize=5'"
                  target="_blank"
                >{{news.caption}}</a>
              </li>
            </template>
          </ul>
        </div>
      </div>
      <div class="newsShowByType_rightBottom">
        <div class="center" style="width:150px;">
          <a id="previous" @click="prePage()" href="javascript:void(0);">上一页</a>
          <a id="next" @click="nextPage()" href="javascript:void(0);">下一页</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//qs是一个npm仓库所管理的包，安装方式：npm install qs命令进行安装，
//引入qs工具，进行提交数据的格式转换
import Qs from "qs";

export default {
  name: "showNewsByNewsType",

  data() {
    return {
      newses: {},
      pageInformation: 0,
      newsType: ""
    };
  },

  created: function() {
    //this.$route.query.newsType表示从main.vue的链接中传递过来的参数newsType
    console.log(this.$route.query.newsType);
    //展现新闻类别对应的新闻列表
    this.newsTypeClick(this.$route.query.newsType);
  },

  methods: {
    //展现新闻类别对应的新闻列表
    newsTypeClick: function(newsType1) {
      this.newsType = newsType1;

      this.getNewsByType();
    },
    //前一页
    prePage: function() {
      if (this.pageInformation.page > 1) this.pageInformation.page--;

      this.getNewsByType();
    },
    //后一页
    nextPage: function() {
      console.log(this.pageInformation);
      if (this.pageInformation.page < this.pageInformation.totalPageCount)
        this.pageInformation.page++;

      this.getNewsByType();
    },
    ////展现新闻类别对应的新闻列表
    getNewsByType: function() {
      var url =
        "/servlet/NewsServlet?condition=showNewsByNewsTypeAjaxVue&newsType=" +
        this.newsType;

      if (this.pageInformation == 0) {
        //防止 点击main.vue的链接时this.pageInformation为null
        this.pageInformation = {};
        this.pageInformation.page = 1;
        this.pageInformation.pageSize = 10;
      }
      //收集需传递的参数
      var data = Qs.stringify(this.pageInformation);
      //利用axios发出get请求
      this.$http
        .post(url, data, {
          headers: { "Content-Type": "application/x-www-form-urlencoded" }
        }) //设置提交Content-Type类型，无文件域的一般表单，可以设置为'application/x-www-form-urlencoded'
        .then(response => {
          if (response.status === 200) {
            //200表示请求成功，200请求成功、303重定向、400请求错误、401未授权、403禁止访问、404文件未找到、500服务器错误
            console.log(response.data); //console.log：输出参数数据到浏览器控制台，可以观察数据
            this.pageInformation = response.data[0];
            this.newses = response.data[1];
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style type="text/css">
.newsShowByType_left li:hover {
  color: red;
}
.newsShowByType_left ul {
  list-style-type: none;
}

#newsShowByTypeUL li {
  text-align: left;
}
</style>


